.App {
  text-align: center;
  font-family: 'Marmelad', sans-serif;
  color: #ffffff;
  height: 100vh;
  background-repeat: no-repeat;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

body {
  background: rgba(98, 125, 77, 1);
  background: -moz-linear-gradient(45deg, rgba(98, 125, 77, 1) 0%, rgba(208, 240, 24, 1) 19%, rgba(15, 168, 160, 1) 46%, rgba(24, 96, 240, 1) 73%, rgba(31, 59, 8, 1) 100%);
  background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(98, 125, 77, 1)), color-stop(19%, rgba(208, 240, 24, 1)), color-stop(46%, rgba(15, 168, 160, 1)), color-stop(73%, rgba(24, 96, 240, 1)), color-stop(100%, rgba(31, 59, 8, 1)));
  background: -webkit-linear-gradient(45deg, rgba(98, 125, 77, 1) 0%, rgba(208, 240, 24, 1) 19%, rgba(15, 168, 160, 1) 46%, rgba(24, 96, 240, 1) 73%, rgba(31, 59, 8, 1) 100%);
  background: -o-linear-gradient(45deg, rgba(98, 125, 77, 1) 0%, rgba(208, 240, 24, 1) 19%, rgba(15, 168, 160, 1) 46%, rgba(24, 96, 240, 1) 73%, rgba(31, 59, 8, 1) 100%);
  background: -ms-linear-gradient(45deg, rgba(98, 125, 77, 1) 0%, rgba(208, 240, 24, 1) 19%, rgba(15, 168, 160, 1) 46%, rgba(24, 96, 240, 1) 73%, rgba(31, 59, 8, 1) 100%);
  background: linear-gradient(45deg, rgba(98, 125, 77, 1) 0%, rgba(208, 240, 24, 1) 19%, rgba(15, 168, 160, 1) 46%, rgba(24, 96, 240, 1) 73%, rgba(31, 59, 8, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#627d4d', endColorstr='#1f3b08', GradientType=1);
  background-repeat: no-repeat;
  font-family: 'Marmelad', sans-serif;
  color: #ffffff;
}

.App-header {
  /* background-color: #282c34; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #ffffff;
}
.App-header p{
  margin-right: 10px;
}
.App-header button:focus{
outline: none;
}
.App-header button:active{
outline: none;
}

.App-link {
  color: #61dafb;
}
#root{
  height: 100vh;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


